#app {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100vh;
  -ms-flex-align: center;
      align-items: center;
}
#app svg {
  width: 500px;
  height: auto;
}
#app svg circle {
  -webkit-animation: drawCircle 1s 1s ease-in both;
          animation: drawCircle 1s 1s ease-in both;
}
#app svg path {
  -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
          transform-origin: center center;
  -webkit-animation: drawPath 1s 2s ease-in both;
          animation: drawPath 1s 2s ease-in both;
}
@-webkit-keyframes drawPath {
  0% {
    stroke-dasharray: 55;
    stroke-dashoffset: 55;
    opacity: 0;
  }
  70% {
    opacity: 1;
  }
  100% {
    stroke-dasharray: 55;
    stroke-dashoffset: 0;
    opacity: 1;
  }
}
@keyframes drawPath {
  0% {
    stroke-dasharray: 55;
    stroke-dashoffset: 55;
    opacity: 0;
  }
  70% {
    opacity: 1;
  }
  100% {
    stroke-dasharray: 55;
    stroke-dashoffset: 0;
    opacity: 1;
  }
}
@-webkit-keyframes drawCircle {
  0% {
    stroke-dasharray: 70;
    stroke-dashoffset: -70;
    opacity: 0;
  }
  70% {
    opacity: 1;
  }
  100% {
    stroke-dasharray: 70;
    stroke-dashoffset: 0;
    opacity: 1;
  }
}
@keyframes drawCircle {
  0% {
    stroke-dasharray: 70;
    stroke-dashoffset: -70;
    opacity: 0;
  }
  70% {
    opacity: 1;
  }
  100% {
    stroke-dasharray: 70;
    stroke-dashoffset: 0;
    opacity: 1;
  }
}



